.profile {
    border: 1px solid var(--dark-text-color);
    border-radius: 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 350px;
    background: var(--bg);
  }
  .description {
    padding: 30px;
    font-size: 24px;
    color: var(--dark-text-color);
  }
  .avatar {
    border-radius: 50%;
    box-shadow: -10px -10px 5px var(--dark-text-color);
    width: 250px;
    height: 250px;
    display: flex;
    justify-content: center;
  }
  .name {
    font-size: 30px;
    color: var(--black-text-color);
    display: flex;
    justify-content: center;
  }
  .tag {
    display: flex;
    justify-content: center;
  }
  .location {
    display: flex;
    justify-content: center;
  }
  .stats {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  .statsWrap {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 1px solid var(--dark-text-color);
    background: var( --bg-next);
    padding: 20px 25px;
    border-radius: 5px;
  }
  .label {
    font-size: 18px;
    color: var(--black-text-color);
  }
  .quantity {
    font-weight: bold;
    font-size: 20px;
    color: var(--black-text-color);
  }