.item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 48px;
    padding: 20px 30px;
    border: 1px solid var(--dark-text-color);
    border-radius: 5px;
    margin-bottom: 5px;
  }
  .statusOnline {
    display: block;
    width: 20px;
    height: 20px;
    margin-right: 8px;
  
    border-radius: 50%;
    background-color: green;
  }
  .statusOffline {
    display: block;
    width: 20px;
    height: 20px;
    margin-right: 8px;
    border-radius: 50%;
    background-color: red;
  }
  .avatar {
    margin-right: 8px;
  }
  .name {
    margin: 0;
  }